import './bootstrap.scss';
import '@fortawesome/fontawesome-free/css/all.css';
import './components/autocomplete.scss';
import './components/breadcrumb.scss';
import './components/header.scss';
import './components/heading.scss';
import './components/main.scss';
import './components/pattern-input.scss';
import './components/signin.scss';
import './components/statpanel.scss';
import './components/table.scss';
import './components/tag.scss';
import './components/user.scss';
import './components/filter.scss';
import './components/input.scss';
import './components/icons.scss';
import { confirmModal } from './utils/confirm';
import Nav from './components/nav';
import Dropdown from './components/dropdown';
import Sidebar from './components/sidebar';
import Modal from './components/modal';
import NotificationCenter from './components/notificationCenter';
import Tabnav from './components/tabnav';
import { on, dispatch } from './utils/events';
document.addEventListener('DOMContentLoaded', function () {
    NotificationCenter.init();
    document.querySelectorAll('[data-sf="dropdown"]').forEach(function (elem) { return Dropdown.init(elem); });
    document.querySelectorAll('[data-sf="modal"]').forEach(function (elem) { return Modal.init(elem); });
    document.querySelectorAll('[data-sf="tabnav"]').forEach(function (elem) { return Tabnav.init(elem); });
    Nav.init();
    Sidebar.init();
    setTimeout(function () { return document.body.classList.add('loaded'); }, 1);
    document.addEventListener('sf:confirm', function (e) {
        // eslint-disable-next-line max-len
        confirmModal(e.detail.title, e.detail.body, e.detail.options).then(e.detail.onConfirm).catch(e.detail.onCancel);
    });
});
export { on, dispatch, confirmModal, Dropdown, Modal, Tabnav, NotificationCenter, };
